body {
  font-family: Arial, sans-serif;
  margin: 0;
}

.container {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}

.block {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 300px;
  width: 33.33%;
  flex-grow: 1;
  height: 300px;
  font-size: 100px;
  color: #fff;
  text-align: center;
}

.block1 {
  background-color: rgb(255, 0, 0);
}

.block2 {
  background-color: rgb(204, 0, 0);
}

.block3 {
  background-color: rgb(153, 0, 0);
}

.block4 {
  background-color: rgb(102, 0, 0);
}

.block5 {
  background-color: rgb(51, 0, 0);
}

.block6 {
  background-color: rgb(0, 0, 0);
}

@media (min-width: 600px) {
  .block3 {
    order: 2;
  }
  
  .block4 {
    order: 1;
  }
  
  .block5 {
    order: 3;
  }
  
  .block6 {
    order: 4;
  }
}

@media (min-width: 900px) {
  .block3 {
    order: 1;
  }
  
  .block4 {
    order: 4;
  }
  
  .block5 {
    order: 3;
  }
  
  .block6 {
    order: 2;
  }
}
